#CheckforContinue{
    height: 400px;
    width: 500px;
    background-color: rgb(22, 21, 21);
    margin: 200px auto;
    border-radius: 8px;
    color: white;
    font-family: Calibri;
    display: flex;
    flex-direction: column; 
    align-items: center;
}

#CheckforContinue h2{
   margin-top: 80px;
}


#CheckforContinue h3{
    margin-top: 30px;
    font-size: 17px;
    opacity: 0.8;
}



#formtocontinue{
    height: 40px;
    width:100%;
    text-align: center;
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
}


#formtocontinue input{
    background-color: transparent;
    outline: none;
    height: 40px;
    color: white;
    background-color: rgb(41, 41, 41);
    border-width: 0px;
    border-radius: 4px;
    font-size: 15px;
    padding: 12px;
}




#CheckforContinue button{
    height: 40px;
    width: 100px;
    background-color: aqua;
    border-radius: 8px;
    outline: none;
    border-width: 0px;
    margin-top: 50px;
    cursor: pointer;
    transition: .3s ease all ;
}