#HomePage{
    height: 100vh;
    width: 100%;
    /* border: 1px solid white; */
    display: flex;
    flex-direction: column;
    align-items: center;
}



#title{
    text-align: center;
    color: white;
    font-family: Calibri;
    font-size: 60px;
    margin-top: 100px;
}



#SelectAccount{
    height: 500px;
    width: 800px;
    /* border: 1px solid white; */
    margin-top: 70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

#twoaccounts{
    height: 450px;
    background-color: rgb(22, 21, 21);
    width: 380px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
}


#twoaccounts:hover{
 cursor: pointer;
 border: 1px solid rgb(59, 59, 59);
}



#GenderIcon{
 height: 50px;
 width: 50px;
 margin-top: 120px;
}


#NameOfUser{
    font-family: Calibri;
    color: white;
    margin-top: 20px;
    font-size: 50px;
}


#hint{
    margin-top: 20px;
    opacity: 0.5;
    color: white;
    font-family: Calibri;
}

